/*共通部分*/
    * {
        margin: 0;
        padding: 0;
        text-align: center;
    }

@import url('https://fonts.googleapis.com/css?family=Heebo:500|M+PLUS+Rounded+1c&display=swap&subset=japanese');

/*config*/
body{
    margin:0; padding:0;
    color: black;
    background-color: #fff;
  }

/*ヘッダー部分*/


    .container {
        max-width: 980px;
        padding: 0 30px;
    }

    .header {
        margin-left: 0;
        height: 62px;
        width: 100%;
        box-shadow: 0 0 10px;
        background-color: #004752;
        position: fixed;
        top: 0;
        z-index: 9999;
    }

    .header-left {
        height: 56px;
        margin-top: 20px;
        margin-left: 30px;

        margin-right: 3px;
        float: left;
    }

    .header-right {
        float: right;
    }

    .header-logo {
        color: white;
    }

    .clear {
        clear: both;
    }

    .header-right a {
        line-height: 62px;
        color: snow;
        margin-left: 7px;
        font-size: 16px;
        padding: 16px 5px;
    }

    .header-right a:hover {
        background-color: #006c7d;
        transition: background-color 0.5s;
    }

    a {
        text-decoration: none;
    }

    .container {
        margin-left: auto;
        margin-right: auto;
        position: relative;
        min-height: 100%;
        height: 100%;

    }
    .btn-blue {
        display: inline-block;
        padding: 0.3em 1em;
        text-decoration: none;
        color: #67c5ff;
        border: solid 2px #67c5ff;
        border-radius: 3px;
        transition: .4s;
      }
      
      .btn-blue:hover {
        background: #67c5ff;
        color: white;
      }
.notice{
    background-color: #ff6767;
    color:#fff;
    text-align: center;
}

.top-img{
    border-radius: 50%;
    width: 250px;
    height: 250px;
  }

  .main-header{
    border-bottom: solid 3px skyblue;
    position: relative;
  }
  
  .main-header:after {
    position: absolute;
    content: " ";
    display: block;
    border-bottom: solid 3px #77e469;
    bottom: -3px;
    width: 30%;
  }

.topBn li{
    display:inline-block;
    margin:5px 2px;
}
.topBn li img {
    border:solid 1px #ccc;
}